import { useState, useEffect, useRef } from 'react'
import './App.css';
import monster from './monster';

function App() {
  let timer = useRef();
  let [activeID, setActivityID] = useState("");
  let [pet, setPet] = useState(null);
  let [hp, setHp] = useState(null);
  let [loading, setLoading] = useState(true);
  let [petatk, setPetatk] = useState(null);
  let [enermy, setEnermy] = useState(null);
  let [level, setLevel] = useState(-1);
  let [enermyhp, setEnermyhp] = useState(null);
  let [enermyatk, setEnermyatk] = useState("");
  let [coin,setCoin] = useState(null);
  useEffect(() => {
    timer.current = setInterval(() => {

      setCoin(localStorage.getItem("coin")||0);
      //初始化关卡
      if (level === -1) {
        setLevel(localStorage.getItem("level") || 1);
        return;
      }

      //初始化敌人
      if (enermy === null) {
        const m = Object.create(monster[Math.floor(Math.random() * 2)]);
        console.log(level);
        m.hpbase = Math.ceil(m.hpbase* level + Math.random() * Math.sqrt(level) * level);
        m.atkbase = Math.ceil(m.atkbase * level+ Math.random() * Math.sqrt(level) * level);
        m.defbase = Math.ceil(m.defbase * level+ Math.random() * Math.sqrt(level) * level);
        setEnermy(m);
        setEnermyhp(m.hpbase);
      }

      //初始化宠物
      if (pet === null) {
        let activeID = localStorage.getItem("activeID");
        setActivityID(activeID);
        if (!activeID)
          return;
        let pet = (JSON.parse(localStorage.getItem("pets")));
        setPet(pet[activeID]);
        setHp(pet[activeID].monProps[0]);
      }

      if (petatk === null)
        setPetatk("atk");

      if (activeID && pet && enermy && petatk != null) {
        setLoading(false);
        if (enermyatk === "") {
          if (hp <= 0) {
            setPetatk(null);
            setEnermy(null);
            setEnermyatk("");
            setLoading(true);
            setPet(null);
            setHp(pet.monProps[0]);
            return;
          }

          let tem = enermyhp -
            Math.max(0, (pet.monProps[1] - enermy.defbase) +
              Math.floor(Math.random() * pet.monProps[1] - pet.monProps[1] / 2));
          setEnermyhp(tem);
          setPetatk("");  
          if(enermyhp>0){
            setEnermyatk("atk");
          }else{
            setEnermyatk("-");
 }
        } else if (petatk === "") {
          if (enermyhp <= 0) {
            setPetatk(null);
            setPet(null);
            setEnermy(null);
            setLevel(parseInt(level) + 1);
            localStorage.setItem("level", parseInt(level) + 1);
            setHp(pet.monProps[0]);
            setLoading(true);
            setEnermyatk("");
            return;
          }

          hp -= Math.max(0, (enermy.atkbase - pet.monProps[2]) +
            Math.floor(Math.random() * enermy.atkbase - enermy.atkbase / 2));
          setHp(hp);
          
         setEnermyatk("");
          if (hp > 0) {
           setPetatk("atk");
          }else {
           setPetatk("-");
          }




        }
      }
    }, 1000);

    return () => {
      clearInterval(timer.current);
    }
  });

  return (
    <div className="fight-container">
      {
        activeID && pet && enermy ?
          <div>
            <h1>第{level}关</h1>
            <div>时空能量：{coin}</div>
            <div className="fight">
              <div className="img-container">
                <img className={petatk} src={pet.dataURL} alt="" />
              </div>
              <div className="img-container enermy">
                <img className={enermyatk} src={enermy.imgURL} alt="" />
              </div>
            </div>
            <div className="props-container">
              <div className="props">
                <b>我方血量：<progress className="HP-progress" max={pet.monProps[0]} value={hp}></progress></b>
                <p>血量:{hp}/{pet.monProps[0]}<button className="props-plus"
                onClick={()=>{
                  if(coin>=100){
                    setCoin(coin-100);
                    localStorage.setItem("coin",coin-100);
                    let ps = 
                    JSON.parse(localStorage.getItem("pets"));
                    
                    let id = localStorage.getItem("activeID");
                    ps[id].monProps[0] -= (-level); 
                    pet.monProps[0] -= (-level); 
                    localStorage.setItem("pets",JSON.stringify(ps));
                  }
                }}>+</button></p>
                <p>攻击:{pet.monProps[1]}<button className="props-plus"
                  onClick={()=>{
                    if(coin>=100){
                      setCoin(coin-100);
                      localStorage.setItem("coin",coin-100);
                      let ps = 
                      JSON.parse(localStorage.getItem("pets"));
                      
                      let id = localStorage.getItem("activeID");
                      ps[id].monProps[1] -= (-level); 
                      pet.monProps[1] -= (-level); 
                      localStorage.setItem("pets",JSON.stringify(ps));
                    }
                  }}>+</button></p>
                <p>防御:{pet.monProps[2]}<button className="props-plus"  onClick={()=>{
                  if(coin>=100){
                    setCoin(coin-100);
                    localStorage.setItem("coin",coin-100);
                    let ps = 
                    JSON.parse(localStorage.getItem("pets"));
                    
                    let id = localStorage.getItem("activeID");
                    ps[id].monProps[2] -= (-Math.ceil(Math.sqrt(level))); 
                    pet.monProps[2] -= (-Math.ceil(Math.sqrt(level)));
                    localStorage.setItem("pets",JSON.stringify(ps));
                  }
                }}>+</button></p>
              </div>
              <div className="props">
                <b>敌方血量：<progress className="HP-progress" max={enermy.hpbase} value={enermyhp}></progress></b>
                <p>血量:{enermyhp}/{enermy.hpbase}</p>
                <p>攻击:{enermy.atkbase}</p>
                <p>防御:{enermy.defbase}</p>
              </div>
            </div>
          </div>
          :
          loading ? <h1>Loading...</h1> : <h1>请先设置上场宠物</h1>
      }
    </div>

  );
}

export default App;
